@import '../../App.scss';

#contact {
    background: #f5f5f5;
}

// .module-header{
//     margin-bottom: 20px;
// }
.contact-container {
    margin: 0 auto;
    width: 75%;
    display: grid;
    padding-top: 1rem;
    grid-template-columns: 30% 58%;
    gap: 1.2rem;

    .contact-options {
        display: flex;
        flex-direction: column;
        gap: 1.2rem;

        .contact-option {
            background: #F5F5F5;
            padding: 1.2rem;
            border-radius: 0.3rem;
            text-align: center;
            border: 1px solid transparent;
            box-shadow: 1px 2px 3px;
            transition: all 400ms ease;

            &:hover {
                background: $text-color;
                box-shadow: none;
                border-color: #3333;
                color: $color-1;
            }

            .contact-option-icons {
                font-size: 1.5rem;
                margin-bottom: 0.5rem;
            }

            a {
                margin-top: 0.7rem;
                display: inline-block;
                font-size: 0.9rem;
                font-weight: 500;
                text-decoration: none;
                color: blue;
            }

            h4 {
                font-size: 20px;
            }

            h5 {
                font-size: 15px;
            }

        }

    }
}

/*===================== FORM =====================*/

form {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;

    input,
    textarea {
        width: 100%;
        font-size: 1.5rem;
        padding: 1.5rem;
        border-radius: 0.3rem;
        background: transparent;
        border: 2px solid #3333;
        resize: none;
    }

    .btn {
        color: $color-1;
        font-size: 15px;
        font-weight: 400;
        letter-spacing: 4px;
        border-radius: 0.3rem;
        font-family: sans-serif;
        text-decoration: none;
        padding: 19px 18px;
        border: solid 1px #3333;
        animation: fadeInAnimation 1s 1.8s backwards;
        white-space: nowrap;
        position: relative;
        z-index: 1;

        &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 100%;
            height: 100%;
            border-radius: 0.3rem;
            z-index: -2;
            background-color: $text-color;
        }

        &:before {
            content: '';
            color: $text-color;
            position: absolute;
            bottom: 0;
            left: 0;
            width: 0%;
            height: 100%;
            border-radius: 0.3rem;
            background-color: #f1f1f1;
            transition: all .3s;
            z-index: -1;
        }

        &:hover {
            color: $text-color;
            border: solid 1px $text-color;

            &:before {
                width: 100%;
            }
        }
    }
}

.notification-wrapper {
    position: fixed;
    top: 10px;
    right: 10px;
    width: 300px;
}

.notification-item {
    box-shadow: 0 0 5px rgba($color: #000000, $alpha: 0.4);
    border-radius: 10px;
    overflow: hidden;
    animation: SlideLeft 0.3s;
    animation-fill-mode:forwards ;
    width: 300px;
    p {
        margin: 0;
        padding: 10px;
    }
    
    .bar {
        height: 10px;
        background-color: #333333;
    }
    
}
.error{
    .bar{
        height: 10px;
        background-color: red;
    }
}
.success{
    .bar {
        height: 10px;
        background-color: green;
    }
}
.exit{
    animation: SlideRight 0.3s;
    animation-fill-mode:forwards ;
}
@keyframes SlideLeft {
    0% {
        margin-left: 120%;
    }
    100%{
        margin-left: 0%;
    }
}
@keyframes SlideRight {
    0% {
        margin-left: 0%;
    }
    100%{
        margin-left: 120%;
    }
}

/* ======================== MEDIA QUERIES (MEDIUM DEVICES) ======================== */
@media screen and (max-width: 1024px) {
    .container.contact-container {
        grid-template-columns: 1fr;
        gap: 2rem;
    }
}

/* ======================== MEDIA QUERIES (SMALL DEVICES) ======================== */
@media screen and (max-width: 600px) {
    .container.contact-container {
        width: $container-width-sm;

    }
}