@import '../../App.scss';

.col-portfolio {
    font-weight: 400;
    line-height: 1.4;
    font-size: 22px;
    text-align: start;

    .gallery-my-works {
        display: flex;
        justify-content: center;
        flex-wrap: wrap;
        width: 80%;
        margin: 50px auto 0;
        max-width: 900px;
    }

    .all-projects-item {
        border: 1px solid rgba($color: #333333, $alpha: 0.3);
        box-shadow: 1px 2px 3px;
        background: #3333;
        border-radius: 0.3rem;
        height: 10rem;
        width: 40%;
        position: relative;
        // display: flex;
        // align-items: center;
        // justify-content: center;
        // padding: 5px;
        margin: 10px;
        overflow: hidden;
        flex-shrink: 0;

        // &:hover {
        //     background: #F5F5F5;
        //     box-shadow: none;

        // }

        &:hover .hover {
            opacity: 1;
            transform: scale(1.1);
            transition: all 0.3s ease 0s;
            height: 85%;
            width: 85%;
            top: 2px;
            left: 11px;

        }

        img {
            width: 100%;
            height: 100%;
            display: block;
        }

        .text {
            color: #fff;
            font-size: 10px;
            line-height: 1.5em;
            text-shadow: 2px 2px 2px #000;
            text-align: center;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            width: 100%;
        }

        .hover {
            position: absolute;
            height: 100%;
            width: 100%;
            border-radius: 0.3rem;
            opacity: 0;
            transition: .5s ease;
            background: -webkit-linear-gradient(45deg, #000000bd 0%, #f5f5f5 100%);
            // bottom: 5px;
            // right: -8px;
            left: -10px;
            top: -8px;
            opacity: 0;
            // padding: 25px;
            margin: 10px;
            position: absolute;
            text-align: center;
        }
    }

    .article-popup {
        height: 100%;

        .header-popup {
            height: 10%;
            text-align: center;

            .content {
                height: 100%;
                h3{
                    height: 50%;
                }
                .border {
                    width: 100%;
                    height: 1px;
                    border: 0.5px solid #eee;
                    margin: 20px auto;
                }
            }

        }

        .caroussel-popup {
            height: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .info-popup {
            height: 30%;
            display: flex;

            .techno-used {
                width: 50%;
                margin-top: 20px;
                padding: 0 20px;
                .border {
                    width: 100%;
                    height: 1px;
                    border: 0.5px solid #eee;
                    margin: 20px auto;
                }
                .skills-details {
                    ul {
                        padding-left: 30px;

                        li {
                            list-style: disc;

                            abbr {
                                border-bottom: 1px dotted #777;
                            }
                        }
                    }
                }
            }

        }

        .footer-popup {
            height: 10%;
            display: flex;
            flex-direction: column;

            .border{
                width: 100%;
                height: 1px;
                border: 0.5px solid #eee;
                margin: 10px auto;
            }
            .btn {
                margin: 0 auto;
                color: $color-1;
                font-size: 13px;
                font-weight: 400;
                letter-spacing: 4px;
                border-radius: 0.3rem;
                font-family: sans-serif;
                text-decoration: none;
                padding: 19px 18px;
                border: solid 1px #3333;
                animation: fadeInAnimation 1s 1.8s backwards;
                white-space: nowrap;
                position: relative;
                z-index: 1;
                cursor: pointer;
              
                &:after {
                    content: '';
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    border-radius: 0.3rem;
                    z-index: -2;
                    background-color: $text-color;
                }
              
                &:before {
                    content: '';
                    color: $text-color;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    width: 0%;
                    height: 100%;
                    border-radius: 0.3rem;
                    background-color: #f1f1f1;
                    transition: all .3s;
                    z-index: -1;
                }
              
                &:hover {
                    color: $text-color;
                    border: solid 1px $text-color;
              
                    &:before {
                        width: 100%;
                    }
                }
            }
        }
    }
}
@media screen and (max-width: 600px) {
    .gallery-my-works{
        padding-bottom: 50px;
    }
}
